	module.exports = {"data":{"site":{"siteMetadata":{"title":"Nocturnidades","author":"Enrique Moriarty"}},"markdownRemark":{"id":"/Users/enric/Workspace/personal/nocturnidades-blog/nocturnidades/src/pages/relatos/pues-le-rece-pero-no-me-ha-hecho-caso/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/pues-le-rece-ero-no-me-ha-hecho-caso-70ce97362213f8736896e79d4c38292d-a5ccc.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 123.33333333333331%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Pues le recé, pero no me ha hecho caso\"\n        title=\"\"\n        src=\"/static/pues-le-rece-ero-no-me-ha-hecho-caso-70ce97362213f8736896e79d4c38292d-f8fb9.jpg\"\n        srcset=\"/static/pues-le-rece-ero-no-me-ha-hecho-caso-70ce97362213f8736896e79d4c38292d-e8976.jpg 148w,\n/static/pues-le-rece-ero-no-me-ha-hecho-caso-70ce97362213f8736896e79d4c38292d-63df2.jpg 295w,\n/static/pues-le-rece-ero-no-me-ha-hecho-caso-70ce97362213f8736896e79d4c38292d-f8fb9.jpg 590w,\n/static/pues-le-rece-ero-no-me-ha-hecho-caso-70ce97362213f8736896e79d4c38292d-85e3d.jpg 885w,\n/static/pues-le-rece-ero-no-me-ha-hecho-caso-70ce97362213f8736896e79d4c38292d-a5ccc.jpg 900w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>¡Pues le recé, pero no me ha hecho caso!— gritó Vicenta a Manuela, que estaba un poco sorda, cuando se cruzaron en el vestíbulo del edificio. Manuela es la vecina del segundo derecha y la residente más antigua. Días atrás, Vicenta —que es la portera del inmueble —mantuvo una conversación con Manuela sobre la hospitalización de su marido. Manuela, que es muy creyente, le aconsejó que rezara varias plegarias a San Antón, que siempre trae buena solución. Lo que Manuela no sabía cuando le dió el consejo era lo que Vicenta pensaba exáctamente. La auténtica preocupación de Vicenta no era la gravedad del estado de salud de su marido. ¡Que va!. Lo que realmente le preocupaba era que los médicos habían descubierto que la causa del ingreso había sido por una extraña intoxicación.</p>","frontmatter":{"title":"Pues le recé, pero no me ha hecho caso","date":"05 marzo 2017"}}},"pathContext":{"slug":"/relatos/pues-le-rece-pero-no-me-ha-hecho-caso/","previous":{"fields":{"slug":"/articulos/anotaciones-contemporaneas/"},"frontmatter":{"title":"Anotaciones maquiavélicas"}},"next":{"fields":{"slug":"/articulos/nostalgia-y-cambio/"},"frontmatter":{"title":"Nostalgia y cambio"}}}}

